



























import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import { jobsModule, routerModule } from "@/store/modules";
import { distanceInWordsToNow, parse } from "date-fns";
import { DateFormatter } from "@/mixins";
import { ISnapshot } from "@/store/modules/RepositoriesModule";
import { chipColors } from "@/misc/chips";

@Component({
  components: {}
})
export default class SnapshotsList extends Mixins(DateFormatter) {
  @Prop({
    type: Array,
    default: () => []
  })
  snapshots!: ISnapshot[];

  get detailedSnapshots() {
    return this.snapshots.map(s => ({
      ...s,
      relativeDate: this.$relativeDate(s.date),
      jobName:
        jobsModule.getById(s.jobId || 0)!.name ||
        this.$t("snapshot-list.no-job")
    }));
  }

  @Prop({
    type: [String, Number],
    required: true
  })
  repoId!: string | number;

  get chipClass() {
    return chipColors.job;
  }
}
